* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

h1 {
  margin: 1% 3%;
  font-size: 40px;
  line-height: 1.5;
}

.nav {
  position: sticky;
  top: 0;
  overflow: hidden;
}

.title-header {
  letter-spacing: 20px;
  font-size: 3.2rem;
}

.nav ul {
  background-color: #333;
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: sticky;
  top: 0;
  overflow: hidden;
}

.nav li {
  float: left;
}

.nav li a {
  color: #fff;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  display: block;
}

.nav li a:hover {
  background-color: #111;
}

.nav .active {
  background-color: #2b6ebb;
}

.foot {
  color: #fff;
  text-align: center;
  background-color: #111;
  flex-wrap: wrap;
  grid-template-columns: repeat(3, 1fr);
  align-content: center;
  gap: 1.5rem;
  width: 100%;
  min-height: 50vh;
  padding: 5% 10%;
  display: grid;
}

.foot h4 {
  padding-bottom: 2%;
  font-size: 1.8rem;
  position: relative;
}

.foot h4:before {
  width: 50px;
  height: 2px;
  font-size: 1.8rem;
  bottom: -8px;
  left: 0;
}

.foot ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  overflow: hidden;
}

.foot ul li a {
  color: #9897a9;
  text-align: center;
  text-transform: capitalize;
  margin-top: 2%;
  padding: 0;
  font-size: 1.2rem;
  font-weight: 600;
  text-decoration: none;
  transition: all .4s;
  display: block;
}

.foot ul li a:hover {
  color: gray;
  transform: translateX(-10px);
}

@media (max-width: 600px) {
  .foot {
    grid-template-columns: repeat(1, 1fr);
  }
}

.main {
  text-align: center;
  align-content: center;
}

.fist-content {
  border: 1px solid #111;
}

.main-photo {
  text-align: center;
  width: auto;
  max-width: 100%;
  height: auto;
  margin: 5% 0% 0%;
}

.second-content {
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  width: 100%;
  min-height: 90vh;
  padding: 2% 15%;
  display: grid;
}

.second-content img {
  width: 600px;
  max-width: 100%;
  height: auto;
}

.second-content .rigg {
  margin: 5rem 0 2rem;
  padding: 15% 0;
  display: inline-block;
}

.rigg ul {
  margin: 0;
  padding: 5% 0;
  list-style-type: none;
  display: flex;
  overflow: hidden;
}

.rigg ul li {
  padding: 3%;
}

.number {
  letter-spacing: 20%;
  margin: 0 0 5px;
  font-family: TomatoGrotesk;
  font-size: 3.2rem;
  font-weight: 700;
}

@media (max-width: 1520px) {
  .rigg ul {
    padding: 0;
    display: block;
  }

  .second-content {
    grid-template-columns: repeat(2, 1fr);
    padding: 0;
  }

  .second-content .rigg {
    margin: 10% 0;
    padding: 0;
  }

  .rigg ul li {
    padding: 1%;
  }
}

@media (max-width: 800px) {
  .rigg ul {
    padding: 0;
    display: block;
  }

  .second-content {
    grid-template-columns: repeat(1, 1fr);
    padding: 0;
  }

  .second-content .rigg {
    margin: 0;
    padding: 0;
  }

  .rigg ul li {
    padding: 0%;
  }
}

.cat-list {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 30px 15px;
  display: flex;
}

.content {
  grid-gap: 20px;
  grid-template-columns: repeat(3, 1fr);
  max-width: 1000px;
  margin: auto;
  display: grid;
}

.cat-img {
  text-align: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 200px;
  transition: all .2s;
}

.category {
  transition: all .2s;
  box-shadow: 0 1px 6px 1px #0000001a;
}

.category:hover {
  transform: translateY(-4px);
  box-shadow: 0 1px 14px 2px #00000026;
}

.cat-header {
  cursor: pointer;
  width: 100%;
  height: 200px;
  overflow: hidden;
}

.category:hover .cat-img {
  transform: scale(1.1);
}

.cat-body {
  text-align: center;
  padding: 4%;
}

.cat-body span {
  color: #9999;
  margin-bottom: 2%;
  display: inline-block;
}

.cat-body h2 {
  margin-bottom: 10%;
  line-height: 1.5;
}

.cat-link {
  color: #fff;
  background: #2b6ebb;
  border-radius: 20px;
  width: 50%;
  margin: auto;
  padding: 5%;
  text-decoration: none;
  display: block;
  box-shadow: 1px 2px 6px 1px #0000001a;
}

.cat-body .cat-link:hover {
  background: #094963;
  transform: translateY(-2px);
  box-shadow: 1px 2px 6px 1px #0003;
}

@media (max-width: 840px) {
  .content {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .content {
    grid-template-columns: repeat(1, 1fr);
  }
}

.subtitle, .data, .detail {
  margin: 0% 3%;
}

.subtitle, .data {
  font-size: 18px;
  line-height: 1.5;
}

.detail {
  display: flex;
}

.detail-photo {
  width: 30%;
  max-width: 100%;
  height: auto;
  padding: 3%;
}

.detail-text {
  flex-flow: wrap;
  flex: 100px;
  justify-content: space-around;
  align-items: center;
  margin: 2%;
  display: flex;
  overflow: hidden;
}

